.hero{display:flex;align-items:center;justify-content:center;min-height:calc(100vh - 4rem);text-align:center;padding:2rem 0}.content{max-width:1024px;margin:0 auto;padding:0 1.5rem}@media(min-width: 768px){.content{padding:0 2rem}}.title{font-size:2.25rem;line-height:2.5rem;font-weight:700;margin-bottom:1.5rem;background:linear-gradient(to right, #fff, rgb(237.5115207373, 151.1290322581, 243.8709677419));-webkit-background-clip:text;background-clip:text;color:rgba(0,0,0,0);animation:fadeIn .8s ease-out}.actions{display:flex;align-items:center;justify-content:center;gap:1rem;margin-top:2rem}.buttonPrimary,.buttonSecondary{transition-property:color,background-color,border-color,outline-color,text-decoration-color,fill,stroke;transition-timing-function:cubic-bezier(0.4, 0, 0.2, 1);transition-duration:150ms;padding:1rem 2rem;border-radius:.375rem;font-weight:500}.buttonPrimary{background-color:#de3bea;color:#fff}.buttonPrimary:hover{background-color:rgb(205.198156682,23.4193548387,218.5806451613);transform:translateY(-1px)}.buttonPrimary:active{transform:translateY(0)}.buttonSecondary{background-color:#2a2a2a;color:hsla(0,0%,100%,.8)}.buttonSecondary:hover{background-color:rgb(54.75,54.75,54.75);color:#fff}@keyframes fadeIn{from{opacity:0;transform:translateY(10px)}to{opacity:1;transform:translateY(0)}}